@import url('https://fonts.googleapis.com/css2?family=Wallpoet&display=swap');

body{
  font-family:'Wallpoet', sans-serif ;
  color: #FBCD08;
  background-color: #372E18;
}

.app-container{
  display: flex;
  justify-content: center;
  align-items: top;
  height: 100vh;
  border: #FBCD08;
  border-radius: 10px;
  border-style: dashed;
}

.content{
  margin-left: 250px;
  padding: 20px;
  width: 100%;
  transition: margin-left 0.3s;
  
}

.vertical-bar-collapsed + .content{
  margin-left: 50px;
}
.divider{
  border: solid;
  border-color: aliceblue;
  border-radius: 1px;
  border-width: 1px;
  background-color: #FBCD08;
  height: 5px;
  width: 1200px;
  margin-left: 0px;
}

.footer{
  border: solid;
  border-color: aliceblue;
  background-color: antiquewhite;
  border-radius: 1px;
  border-width: 1px;
  height: 15px;
  width: 1200px;
  
}