

.vertical-nav-bar{
    position: fixed;
    top: 0;
    left: 0;
    width: 250px;
    height: 27%;
    background-color: #FBCD08;
    color: white;
    transition: width 0.3s ease-in-out,
                height 0.3s ease-in-out;
    overflow: hidden;
}

.vertical-nav-bar.collapsed{
    width: 100px;
    height: 5%;
    transition: width 0.3s ease-in-out,
                height 0.3s ease-in-out;
}

.toggle-button{
    width: 100%;
    padding: 10px;
    background-color: #FBCD08;
    border: solid;
    border-radius: 5px;
    cursor: pointer;
}

.button-container{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.button-container button{
    width: 90%;
    margin: 10px, 0;
    padding: 11px;
    background-color: #372E18;
    color: white;
    border: solid;
    border-radius: 10px;
    cursor: pointer;
}
.button-hover{
    background-color: aquamarine;
}